import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { OneConfiguratorSlice } from '../../+state/one-configurator.reducers';
import { addSim, removeSim } from '../../+state/one-configurator.actions';
import { Sims } from '../../entities/sims/sims.interface';
import { Observable } from 'rxjs';
import { selectSims } from '../../+state/one-configurator.selectors';

@Injectable({ providedIn: 'root' })
export class SimsFacade {
  sims$: Observable<Sims> = this.store.select(selectSims);

  constructor(private readonly store: Store<OneConfiguratorSlice>) {}

  addSim(): void {
    this.store.dispatch(addSim());
  }

  removeSim(): void {
    this.store.dispatch(removeSim());
  }
}
