import { Actions, createEffect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import { mergeMap, take, tap } from 'rxjs/operators';
import { addSim, removeSim } from './one-configurator.actions';
import { MessageBusService } from '@telenet/ng-lib-personalisation';
import { Store } from '@ngrx/store';
import { OneConfiguratorSlice } from './one-configurator.reducers';
import { selectSims } from './one-configurator.selectors';
import { Sims } from '../entities/sims/sims.interface';

@Injectable({
  providedIn: 'root',
})
export class OneConfiguratorEffects {
  constructor(
    private readonly actions$: Actions,
    private readonly store: Store<OneConfiguratorSlice>,
    private readonly messageBusService: MessageBusService
  ) {}

  notifyAddSim = createEffect(
    () =>
      this.actions$.pipe(
        ofType(addSim),
        mergeMap(() =>
          this.store.select(selectSims).pipe(
            take(1),
            tap((sims: Sims) => {
              console.log('monorepo: notifyAddSim - sending number of sims');
              console.log(JSON.stringify(sims.simConfigurations));
              this.messageBusService.messageBus$.next({
                type: '[monorepo] update number of sims',
                payloadAsJson: JSON.stringify({ length: sims.simConfigurations.length }),
              });
            })
          )
        )
      ),
    { dispatch: false }
  );

  notifyRemoveSim = createEffect(
    () =>
      this.actions$.pipe(
        ofType(removeSim),
        mergeMap(() =>
          this.store.select(selectSims).pipe(
            take(1),
            tap((sims: Sims) => {
              console.log('monorepo: notifyRemoveSim - sending number of sims');
              console.log(JSON.stringify(sims.simConfigurations));
              this.messageBusService.messageBus$.next({
                type: '[monorepo] update number of sims',
                payloadAsJson: JSON.stringify({ length: sims.simConfigurations.length }),
              });
            })
          )
        )
      ),
    { dispatch: false }
  );
}
